 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */

.directia-form {
	max-width: 700px;
	width: 100%;
	background-color: #fff;
	padding: 25px 30px;
	border-radius: 5px;
	box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
}
.directia-form-container .directia-form-title {
	font-size: 25px;
	font-weight: 500;
	position: relative;
}
.directia-form-container .directia-form-title::before {
	content: "";
	position: absolute;
	left: 0;
	bottom: 0;
	height: 3px;
	width: 30px;
	border-radius: 5px;
	background: linear-gradient(135deg, #71b7e6, #9b59b6);
}
.directia-form-content form .directia-form-user-details {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	margin: 20px 0 12px 0;
}
form .directia-form-user-details .directia-form-input-box {
	margin-bottom: 15px;
	width: calc(100% / 2 - 20px);
}

form .directia-form-user-details .directia-form-input-box-full {
	width: 100% !important;
}

form .directia-form-input-box span.directia-form-details {
	display: block;
	font-weight: 500;
	margin-bottom: 5px;
}
.directia-form-user-details .directia-form-input-box input {
	height: 45px;
	width: 100%;
	outline: none;
	font-size: 16px;
	border-radius: 5px;
	padding-left: 15px;
	border: 1px solid #ccc;
	border-bottom-width: 2px;
	transition: all 0.3s ease;
}
.directia-form-user-details .directia-form-input-box input:focus,
.directia-form-user-details .directia-form-input-box input:valid {
	border-color: #9b59b6;
}
form .directia-form-gender-details .directia-form-gender-title {
	font-size: 20px;
	font-weight: 500;
}
form .directia-form-category {
	display: flex;
	width: 80%;
	margin: 14px 0;
	justify-content: space-between;
}
form .directia-form-category label {
	display: flex;
	align-items: center;
	cursor: pointer;
}
form .directia-form-category label .directia-form-dot {
	height: 18px;
	width: 18px;
	border-radius: 50%;
	margin-right: 10px;
	background: #d9d9d9;
	border: 5px solid transparent;
	transition: all 0.3s ease;
}
#dot-1:checked ~ .directia-form-category label .directia-form-one,
#dot-2:checked ~ .directia-form-category label .directia-form-two,
#dot-3:checked ~ .directia-form-category label .directia-form-three {
	background: #9b59b6;
	border-color: #d9d9d9;
}
form input[type="radio"] {
	display: none;
}
form .directia-form-button {
	height: 45px;
	margin: 35px 0;
}
form .directia-form-button input {
	height: 100%;
	width: 100%;
	border-radius: 5px;
	border: none;
	color: #fff;
	font-size: 18px;
	font-weight: 500;
	letter-spacing: 1px;
	cursor: pointer;
	transition: all 0.3s ease;
	background: linear-gradient(135deg, #71b7e6, #9b59b6);
}
form .directia-form-button input:hover {
	/* transform: scale(0.99); */
	background: linear-gradient(-135deg, #71b7e6, #9b59b6);
}
@media (max-width: 584px) {
	.directia-form-container {
		max-width: 100%;
	}
	form .directia-form-user-details .directia-form-input-box {
		margin-bottom: 15px;
		width: 100%;
	}
	form .directia-form-category {
		width: 100%;
	}
	.directia-form-content form .directia-form-user-details {
		max-height: 300px;
		overflow-y: scroll;
	}
	.directia-form-user-details::-webkit-scrollbar {
		width: 5px;
	}
}
@media (max-width: 459px) {
	.directia-form-container .directia-form-content .directia-form-category {
		flex-direction: column;
	}
}

.directia-form-container input[type="text"],
.directia-form-container input[type="password"],
.directia-form-container input[type="email"],
input[type="url"],
.directia-form-container input[type="date"],
input[type="month"],
.directia-form-container input[type="time"],
input[type="datetime"],
.directia-form-container input[type="datetime-local"],
.directia-form-container input[type="week"],
.directia-form-container input[type="number"],
.directia-form-container input[type="search"],
.directia-form-container input[type="tel"],
.directia-form-container input[type="color"],
.directia-form-container textarea {
	-webkit-appearance: none;
	-moz-appearance: none;
	background: #fff;
	border-radius: 0;
	border-style: solid;
	border-width: 0.1rem;
	box-shadow: none;
	display: block;
	font-size: 12px;
	letter-spacing: -0.015em;
	margin: 0;
	max-width: 100%;
	padding: 5px;
	width: 100%;
}
